import { NextRequest, NextResponse } from 'next/server';
import { bundle } from '@remotion/bundler';
import { renderMedia, selectComposition } from '@remotion/renderer';
import path from 'path';
import fs from 'fs';
import { pipeline } from 'stream/promises';
import os from 'os';

export async function POST(req: NextRequest) {
  try {
    const formData = await req.formData();
    const file = formData.get('file') as File;
    const inputPropsStr = formData.get('inputProps') as string;

    if (!file || !inputPropsStr) {
      return NextResponse.json({ error: 'Dosya veya ayarlar eksik' }, { status: 400 });
    }

    // 1. Dosyayı Buffer'a çevir ve Data URI oluştur
    // Bu yöntem, dosya yolu/izin/karakter sorunlarını tamamen ortadan kaldırır.
    const buffer = Buffer.from(await file.arrayBuffer());
    const base64 = buffer.toString('base64');
    const mimeType = file.type || 'video/mp4';
    const dataUri = `data:${mimeType};base64,${base64}`;

    // 2. Input Props'u güncelle (Video kaynağı olarak Data URI kullan)
    const inputProps = JSON.parse(inputPropsStr);
    inputProps.videoSrc = dataUri;

    // --- YENİ: Statik görselleri Base64'e çevirip prop olarak ekle ---
    // Bu işlem, render sırasında "Error loading image" hatasını önler.
    const publicDir = path.join(process.cwd(), 'public');
    const readImageAsDataUri = async (filename: string) => {
        try {
            const filePath = path.join(publicDir, filename);
            const fileBuffer = await fs.promises.readFile(filePath);
            const ext = path.extname(filename).replace('.', '');
            return `data:image/${ext === 'svg' ? 'svg+xml' : ext};base64,${fileBuffer.toString('base64')}`;
        } catch (e) {
            console.warn(`Görsel okunamadı: ${filename}`, e);
            return null; // Dosya yoksa null döner, bileşen fallback kullanır
        }
    };

    inputProps.images = {
        appStore: await readImageAsDataUri('appstore.png'),
        google: await readImageAsDataUri('google.png'),
        icon: await readImageAsDataUri('icon.png'),
    };
    // ---------------------------------------------------------------

    // 3. Projeyi Bundle Et (Derle)
    // Not: src/remotion/index.ts dosyasının var olduğu varsayılır
    const entryPoint = path.join(process.cwd(), 'src', 'remotion', 'index.ts');
    const bundleLocation = await bundle({
      entryPoint,
      // Webpack alias (@/...) desteği için yapılandırma
      webpackOverride: (config) => {
        return {
          ...config,
          resolve: {
            ...config.resolve,
            alias: {
              ...(config.resolve?.alias || {}),
              '@': path.join(process.cwd(), 'src'),
            },
          },
        };
      },
    });

    // 4. Kompozisyonu Seç
    const composition = await selectComposition({
      serveUrl: bundleLocation,
      id: 'KidsAIReel', // Remotion Root içindeki ID ile aynı olmalı
      inputProps,
    });

    // 5. Render İşlemi (MP4 Oluştur)
    const tempDir = os.tmpdir();
    const outputLocation = path.join(tempDir, `render-${Date.now()}.mp4`);
    
    await renderMedia({
      composition,
      serveUrl: bundleLocation,
      codec: 'h264',
      outputLocation,
      inputProps,
      chromiumOptions: {
        disableWebSecurity: true,
        ignoreCertificateErrors: true,
        gl: 'swangle', // GPU hatalarını önlemek için yazılımsal render
      },
      crf: 12, // Çok Yüksek Kalite (Varsayılan ~23. Değer düştükçe kalite artar. 12 neredeyse kayıpsızdır.)
      imageFormat: 'png', // Metinlerin netliği için PNG şart
      pixelFormat: 'yuv420p', // Geniş uyumluluk ve doğru renkler için
    });

    // 6. Dosyayı İstemciye Gönder
    const videoBuffer = await fs.promises.readFile(outputLocation);
    
    // Temizlik
    try { await fs.promises.unlink(outputLocation); } catch (e) {}

    return new NextResponse(videoBuffer, {
      headers: {
        'Content-Type': 'video/mp4',
        'Content-Disposition': 'attachment; filename="render.mp4"',
      },
    });

  } catch (error) {
    console.error('Render Hatası:', error);
    return NextResponse.json(
      { error: 'Render işlemi başarısız oldu', details: String(error) }, 
      { status: 500 }
    );
  }
}
